@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: $tables-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline() {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 39px;
  }

  @include onDesktop {
    padding-inline: 55px;
  }
}

@mixin contentPaddingBlock() {
  padding-block: 34px 30px;

  @include onTablet {
    padding-block: 50px 30px;
  }
}

.container {
  @include contentPaddingInline;
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 30px;
    // padding: 0 39px;
  }

  @include onDesktop {
    --columns: 12;
    column-gap: 30px;
  }
}
